import React, { useState } from 'react'
import './AmongUs.css'
import {FaExchangeAlt} from 'react-icons/fa'
import {AiFillGithub} from 'react-icons/ai'
import {FaLinkedinIn} from 'react-icons/fa'

function AmongUs() {
    const [green,setGreen] = useState(false);
    const handleClick = () => {
        setGreen(!green)
    }

    const renderClasses = () => {
        let classes = "player";
        if(green){
            classes += " green"
        }
        return classes
    }
    const renderClasses2 = () => {
        let classes2 = "legs";
        if(green){
            classes2 += " greenlegs";
        }
        return classes2
    }
    const renderClasses3 = () => {
        let classes3 = "back";
        if(green){
            classes3 += " greenback";
        }
        return classes3
    }
    return (
        <>
        <div className='body'>
            <div className={renderClasses()}>
                <div className={renderClasses2()}></div>
                <div className={renderClasses3()}></div>
                <div className="glass"></div>
                <div className="shadow"></div>
            </div>
            
        </div>
        <div className="options">
            <button className='git'><a href="https://github.com/belikeakash/among-us"><AiFillGithub className='link-a' /></a></button>
            <button onClick={handleClick}><FaExchangeAlt/></button>
            <button className='link'><a href="https://www.linkedin.com/in/akash-sirohi-676b30202/"><FaLinkedinIn className='link-a' /></a></button>
        </div>

        
        </>
    )
}

export default AmongUs
